*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.Daftar-container {
  display: flex ;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  min-height: 100vh;
}
.daftar-navbar{
  position: sticky;
  top: 0;
  width: 100%;
  height: 12rem;
  /* border: 1px solid #fff; */
  border-radius: .3rem;
  backdrop-filter: blur(2rem);
  box-shadow: 0 0 1.2rem rgb(0, 0, 0, .5);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}
.daftar-judul {
  margin-top: 0;
  font-size: 2.5rem;
}
.daftar-step{
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  width: 70rem;
  color: #fff;
}
.step{
  margin-right: 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.step p{
  text-shadow: 0 0 2rem#0c6ef8;
}
.step-line{
  display: flex;
  justify-content: center;
  align-items: center;

}
.daftar-indicator{
  /* position: fixed; */
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 15px;
  margin-top: 2rem;
  width: 20px;
  height: 20px;
  color: #000;
  background-color: #fff;
  border-radius: 50%;
  margin-bottom: 10px;
  transition: background-color 0.3s ease;
  cursor: pointer;
}

.daftar-indicator-active{
  background-color: tomato;
}
