.daftar-form{
  margin-top: 0;
  margin-bottom: 2rem;
}
.daftar-judul-form{
  text-align: center;
  padding: 2rem;
}
.daftar-input-box{
  padding: .5rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-size: 1.3rem;
  width: 70rem;
  /* border: 1px solid tomato; */
  margin: .5rem;
}
.daftar-input-box input, select{
  padding: .5rem;
  font-size: 1.3rem;
  border: 1px solid #0c6ef8;
  border-radius: .5rem;
  width: 68rem;
}
.btnNextPrev{
  border: 1px solid #000;
  border-radius: 1rem;
  width: 8rem;
  height: 4rem;
  margin-bottom: 5rem;
  font-size: 1.2rem;
  font-weight: 500;
  background-color: tomato;
  box-shadow: 0 0 1.2rem#0c6ef8;
  margin-right: 1rem;
  cursor: pointer;
}
.daftar-span{
  color: rgb(219, 63, 63);
  text-shadow: 0 0 .2rem #000000;
}